<template>
  <div id="mycontacts" v-loading.fullscreen.lock="loading" element-loading-text="">
    <!--  -->
    <div class="wrap-left" style="height: 793px;">
      <div class="top-search-box clearfix">
        <div class="input-box fl">
          <el-input
            placeholder=""
            icon="search"
            v-model="searchinput"
            :on-icon-click="handleIconClick"
            @keyup.native.enter="handleIconClick">
          </el-input>
        </div>
        <div class="btns-box fr">
          <el-button type="primary" @click="addContacts"></el-button>
        </div>
      </div>
      <div class="top-lists" style="height:690px;cursor: pointer">
        <template>
          <el-table :data="tableData" style="width: 100%"
                    @row-click="handleSelect"
                    @header-click="headerClick"
                    @sort-change="filterChange"
                    @filter-change="filterChange" stripe>
            <el-table-column prop="user_real_name" label="" width="200" show-overflow-tooltip>
              <template scope="scope">
                <div class="img fl">
                  <img v-if="scope.row.user_avatar_url!=''" :src="scope.row.user_avatar_url">
                  <span v-else class="header">{{scope.row.user_avatar_url_change}}</span>
                </div>
                <el-tooltip class="fl name" placement="top" :disabled="scope.row.user_real_name.length > 4 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.user_real_name}}</div>
                  </div>
                  <div>
                    {{scope.row.user_real_name}}
                  </div>
                </el-tooltip>
                <span class="fl add" v-if="scope.row.is_add==false"><img src="../../../assets/images/add.png"></span>
                <div v-if="scope.row.user_real_name.length === 0">
                  --
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="user_company_career" label="" show-overflow-tooltip width="80">
              <template scope="scope">
                <div v-if="scope.row.user_company_career==''">
                  --
                </div>
                <div else>
                  {{scope.row.user_company_career}}
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="card_company_name" label="" show-overflow-tooltip width="144">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.user_company_name.length > 10 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.user_company_name}}</div>
                  </div>
                  <div>
                    {{scope.row.user_company_name}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.user_company_name.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="user_brand" label="" show-overflow-tooltip width="80">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.user_brand.length > 5 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.user_brand}}</div>
                  </div>
                  <div>
                    {{scope.row.user_brand}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.user_brand.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="user_mobile" label="" show-overflow-tooltip width="112">
              <template scope="scope">
                <div v-if="scope.row.user_mobile==''">
                  --
                </div>
                <div else>
                  {{scope.row.user_mobile}}
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="user_email" label="" show-overflow-tooltip width="148">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.user_email.length > 17 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.user_email}}</div>
                  </div>
                  <div>
                    {{scope.row.user_email}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.user_email.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="user_invest_industry" label=""
                             show-overflow-tooltip
                             width="128"
                             column-key="industry"
                             :filters="user_invest_industryFilters"
                             filter-placement="bottom-end">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.user_invest_industry.length > 10 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.user_invest_industry}}</div>
                  </div>
                  <div>
                    {{scope.row.user_invest_industry}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.user_invest_industry.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="user_invest_stage" label="" show-overflow-tooltip
                             width="140"
                             column-key="stage"
                             :filters="user_invest_stageFilters"
                             filter-placement="bottom-end">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.user_invest_stage.length > 10 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.user_invest_stage}}</div>
                  </div>
                  <div>
                    {{scope.row.user_invest_stage}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.user_invest_stage.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="tag" label="" show-overflow-tooltip
                             width="74"
                             column-key="tag"
                             :filters="tagFilters"
                             filter-placement="bottom-end">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.tag.length > 4 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.tag}}</div>
                  </div>
                  <div>
                    {{scope.row.tag}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.tag.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="login_time" label="" show-overflow-tooltip
                             width="100"
                             column-key="login_time"
                             :filters="created_atFilters"
                             :filter-multiple="stateCheck"
                             sortable="custom">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.login_time.length > 4 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.login_time}}</div>
                  </div>
                  <div>
                    {{scope.row.login_time}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.login_time.length === 0">
                  --
                </div>
              </template>
            </el-table-column>

            <el-table-column
              prop="reset"
              label=""
              width="130" class="set-th">
              <template scope="scope">
                <el-button
                  @click="handlePush(scope.$index, scope.row)"
                  type="text"
                  size="small" class="edits-btn btn-cur">
                  
                </el-button>
                <el-button
                  type="text"
                  size="small" class="flow-btn btn-cur" v-if="scope.row.is_bind==0"
                  @click="handleEdit(scope.$index, scope.row)">
                  
                </el-button>
                <el-button
                  type="text"
                  size="small" class="flow-btn btn-cur"
                  v-if="scope.row.is_bind==1"
                  @click="handleTag(scope.$index, scope.row)">
                  
                </el-button>
                <el-button
                  type="text"
                  size="small" class="send-btn btn-cur"
                  @click="handleDelete(scope.$index, scope.row)">
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="timeCheck">
            <el-date-picker
              v-model="timeSelect"
              type="date"
              placeholder=""
              :editable="false"
              @change="timeChange"
              :picker-options="pickerOptions">
            </el-date-picker>
          </div>
          <div class="pagenav" v-if="totalData>10">
            <el-pagination
              small
              @current-change="filterChangeCurrent"
              :current-page.sync="currentPage"
              :page-size="10"
              layout="total, prev, pager, next"
              :total="totalData">
            </el-pagination>
          </div>
        </template>
      </div>
    </div>

    <!---->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :show-close="close"
      size="tiny">
      <el-select
        v-model="tagsValue"
        multiple
        :multiple-limit="multiplelimit"
        filterable
        allow-create
        style="width:100%"
        @change="addChangeTag"
        default-first-option
        placeholder="">
        <el-option
          v-for="item in addTags"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <div class="tagTitle"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addTag"> </el-button>
      </span>
    </el-dialog>

    <!---->
    <projectpush :dialog-push="dialogPushVisible" :user-message="userMessage" :user-email="userEmail"
                 @changeall="dialogVisiblechange" @changeCloseProjectpush="dialogVisiblechangeCloase"></projectpush>

    <!---->
    <projectpreview :dialog-preview-visible="dialogPreviewVisible" @changeCon="dialogPrechange"
                    @closePreview="closePreview" :comeFrom="'contacts'"></projectpreview>

  </div>
</template>

<script type="text/ecmascript-6">
import projectpush from './projectPush.vue'
import projectpreview from './projectPreview.vue'
export default {
  data () {
    return {
      close:false,
      activeName:'second',

      loading: false,//
      dialogUploadVisible:false,//
      searchinput:'',//
      dialogVisible:false,//
      dialogPushVisible:false,//
      dialogPreviewVisible:false,//
      totalData:1,//
      currentPage:1,//
      getPra:{},//
      tagsValue:[],//
      addTags:[],//
      tableData:[
          /*{
            user_avatar_url:"https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83epia77Br2Wk8RiaR8hMAxMG9DerJfzuRCGr5Pf0s2MNDj1FU6dwnpKycchqTRck13S0RTQ6Cg3qZy4A/0",//
            user_avatar_url_change:"",//
            user_real_name:'',//
            is_add: true,//,true,false
            user_company_career:'',//
            user_company_name:'',//
            user_brand:',FA',//
            user_mobile: "18910359282",//
            user_email: "123@168.com",//
            user_invest_industry: "",//
            user_invest_stage: " ",//
            tag:"",//
            tagArray:[],//,
            login_time:"",//
            is_bind:0,//
        }*/
      ],//
      user_invest_industryFilters:[],//
      user_invest_stageFilters:[],//
      tagFilters:[],//
      login_timeFilters:[],//
      userMessage:{
        user_real_name:'',//
        user_company_career:'',//
        user_company_name:'',//
      },//
      userEmail:'',
      tags:{
        changecont:[],//
        index:'',//
        card_id:''//id
      },
      multiplelimit:5,//5
      timeSelect:'',//
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 8.64e7+3600 * 1000 * 24;
        }
      },
      created_atFilters:[],//
      stateCheck:false,//


    }
  },
  components: {
    projectpush,
    projectpreview
  },
  methods: {

    handleSelect(row, event, column) {
      if(column.label!=""){
        this.$router.push({ name: 'contactsDetails', query: { user_id:row.user_id , card_id:row.card_id, investor_id:row.investor_id}})
      }
    },//
    handleEdit(index, row){
      this.$router.push({ name: 'createContacts', query: { card_id:row.card_id}})
    },//,

    getTagId(data){
      let arr = [];
      for(let i=0; i<data.length; i++){
        arr.push(data[i].tag_id);
      }
      return arr;
    },//id
    handleTag(index,row){
        this.dialogVisible = true;
        this.tags.index=index;
        this.tags.card_id=row.card_id;
        this.tagsValue=this.getTagId(this.tableData[index].tagArray);
        this.tags.changecont=this.getTagId(this.tableData[index].tagArray);
    },//
    handleDelete(index,row){
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.loading=true;
        this.$http.post(this.URL.deleteConnectUser, {user_id:localStorage.user_id,card_id: row.card_id})
          .then(res => {
            this.loading=false;
            this.$tool.success("");
            this.handleIconClick();
          })
          .catch(err => {
            this.loading=false;
            this.$tool.error("");
            this.$tool.console(err);
          })

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },//
    headerClick(column, event){
      if(column.label===""){
        window.location.reload();
      }
    },//
    handlePush(index,row){
      this.userMessage.user_real_name=row.user_real_name;
      this.userMessage.user_company_career=row.user_company_career;
      this.userMessage.user_company_name=row.user_company_name;
      this.userMessage.card_id=row.card_id;
      this.userMessage.investor_id=row.investor_id;
      this.userEmail=row.user_email;
      this.dialogPushVisible=true;

    },//,
    addContacts(){
      this.$router.push({name: 'createContacts',query: {card_id: 'creat'}})//
    },//
    dialogVisiblechange(msg){
//      this.dialogPushVisible=msg;
      this.dialogPreviewVisible=true;
    },//,
    dialogVisiblechangeCloase(msg){
      this.dialogPushVisible=msg;
    },//
    dialogPrechange(msg){
      this.dialogPushVisible=msg;
      this.dialogPreviewVisible=msg;
    },//
    closePreview(msg){
      this.dialogPreviewVisible=msg;
    },//
    /**/
    handleIconClick(){
      this.loading=true;
      this.getPra.user_id=localStorage.user_id;
      this.getPra.search=this.searchinput;
      this.currentPage=1;
      this.getPra.page=1;
      this.$http.post(this.URL.getConnectUser,this.getPra)
        .then(res=>{
          let data = res.data.data;
          this.tableData=this.getProjectList(data);
          this.loading=false;
          this.totalData=res.data.count;
        })
        .catch(err=>{
          this.loading=false;
          this.$tool.console(err,2)
        })
    },//===
    timeChange(time){
        console.log(time)
      this.loading=true;
      this.currentPage=1;
      this.getPra.created_at_time=time;
      console.log(this.getPra)
      this.$http.post(this.URL.getConnectUser,this.getPra)
        .then(res=>{
          let data = res.data.data;
          this.tableData=this.getProjectList(data);

          this.totalData=res.data.count;
          this.loading=false;
        })
        .catch(err=>{
          this.loading=false;
          this.$tool.console(err,2)
        })
    },//
    filterChange(filters){
      this.loading=true;
      this.currentPage=1;
      this.getPra.user_id=localStorage.user_id;
      if(filters.order){
        if(filters.order=="ascending") filters.order="asc"//
        else filters.order="desc";
        this.getPra.order=filters.prop;
        this.getPra.sort=filters.order;
      }else{
        for(let key in filters){
          this.getPra[key]=filters[key];
        }
      } //
      for(let key in this.getPra){
        if(this.getPra[key]=='' || this.getPra[key]=='NaN'){
          delete this.getPra[key];
        }
      }//
      this.$tool.console(this.getPra);
      this.$http.post(this.URL.getConnectUser,this.getPra)
        .then(res=>{
          this.loading=false;
          let data = res.data.data;
          this.tableData=this.getProjectList(data);
          this.totalData=res.data.count;
        })
        .catch(err=>{
          this.loading=false
          this.$tool.console(err,2)
        })
    },// ascending/descending/
    filterChangeCurrent(page){
      delete this.getPra.page;
      this.loading=true;
      this.getPra.user_id=localStorage.user_id;
      this.getPra.page=page;//
      this.$tool.console(this.getPra);
      this.$http.post(this.URL.getConnectUser,this.getPra)
        .then(res=>{
          this.loading=false;
          let data = res.data.data;
          this.$tool.console(res);
          this.tableData=this.getProjectList(data);
        })
        .catch(err=>{
          this.loading=false
          this.$tool.console(err,2)
        })
    },//

    titleSift(){
      this.loading=true;
      this.$http.post(this.URL.userTitleSift,{user_id: localStorage.user_id})
        .then(res=>{
          let data = res.data.data;
          let card_industry=data.card_industry;//
          let card_stage=data.card_stage;//
          let card_tag=data.card_tag;//
          this.user_invest_industryFilters=this.$tool.getTitleSift(card_industry);
          this.user_invest_stageFilters=this.$tool.getTitleSift(card_stage);
          this.tagFilters=this.$tool.getTitleSift(card_tag);
          this.loading=false;
        })
        .catch(err=>{
          this.loading=false;
          this.$tool.console(err,2)
        })
    },// 
    setUrlChange(url,name){
        let string='';
        if(url=='') string = name.charAt(0);
        else string='';
        return string;
    },//

    getProjectList(list){
      let arr = new Array;
      for(let i=0; i<list.length; i++){
        let obj=new Object;
        obj.user_id=list[i].user_id;
        obj.user_avatar_url=list[i].user_avatar_url;
        obj.user_real_name=list[i].user_real_name;//
        obj.user_avatar_url_change=this.setUrlChange(list[i].user_avatar_url,list[i].user_real_name);//
        obj.is_add=list[i].is_add;//
        obj.is_bind=list[i].is_bind;//
        obj.user_company_career=list[i].user_company_career;//
        obj.user_company_name=list[i].user_company_name;//
        obj.user_brand=list[i].user_brand;//
        obj.user_mobile=list[i].user_mobile;//
        obj.user_email=list[i].user_email;//
        obj.user_invest_industry=this.$tool.setTagToString(list[i].user_invest_industry,'industry_name');//
        obj.user_invest_stage=this.$tool.setTagToString(list[i].user_invest_stage,'stage_name');//
        obj.tag=this.$tool.setTagToString(list[i].user_invest_tag,'tag_name');//
        obj.tagArray=list[i].user_invest_tag;//
        obj.login_time=list[i].login_time;//
        obj.card_id=list[i].card_id;//
        obj.investor_id=list[i].investor_id;//
        arr.push(obj);
      }
      return arr;
    },//

    getWxProjectCategory(){
        this.addTags = this.$global.data.tags_user;//
//        this.tags.changecont = this.$global.data.tags_user;//2
    },//
    addChangeTag(e){
      let tagName = this.$tool.checkArr(e, this.addTags);
      if (tagName != undefined) {
        this.$http.post(this.URL.createCustomTag, {user_id: localStorage.user_id, type: 3, tag_name: tagName})
          .then(res => {
            let newState = {};
            newState.label = tagName;
            newState.value = res.data.tag_id;
            this.tags.changecont.push(newState);
          })
          .catch(err => {
            this.$tool.error("");
            this.$tool.console(err);
          })
      }
    },//
    addTag(){
      this.loading=true;
      this.$tool.setTag(this.tagsValue,this.tags.changecont);
      this.$http.post(this.URL.setConnectTag, {user_id:localStorage.user_id,card_id: this.tags.card_id,tag: this.tagsValue})
        .then(res => {
          this.loading=false;
          this.$tool.success("");
          this.dialogVisible = false;
          this.handleIconClick();
          this.gettags_user();
        })
        .catch(err => {
          this.loading=false;
          this.$tool.error("");
          this.$tool.console(err);
          this.dialogVisible = false;

        })
    },//
    gettags_user(){
      this.$http.post(this.URL.getWxProjectCategory, {user_id: localStorage.user_id})
        .then(res => {
          let data = res.data.data;
          this.addTags = this.$tool.getTags_pro(data.tags_user);//
        })
    },//


  },
  created(){
    this.loading=true;
    this.$global.func.getWxProjectCategory();
    this.titleSift();
    setTimeout(()=>{
      this.getWxProjectCategory();
      this.handleIconClick();
    },200)

//    zhuge.track('');
  }
}
</script>

<style type="text/css" lang="less">
  @import '../../../assets/css/mycontacts';
</style>



